.themeSwitch {
  position: absolute;
  font-size: 1.2rem;
  padding: 8px;
  bottom: 30px;
  cursor: pointer;
  user-select: none;
  touch-action: none;

  input[type="checkbox"] {
    display: none;
  }

  .icon {
    user-select: none;
    touch-action: none;
  }

  .iconDark {
    cursor: pointer;
    user-select: none;
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
  }
}
